import { Component, OnInit, Input } from '@angular/core';
import { SvgElementComponent } from '../svg-element/svg-element.component';
import { Defaults } from '../defaults.enum';
import { NgxContentLoadingComponent } from '../ngx-content-loading.component';

@Component({
  selector: '[ngx-rect]',
  template: `<svg:rect
        [attr.x]="x"
        [attr.y]="y"
        [attr.rx]="rx"
        [attr.ry]="ry"
        [attr.width]="width"
        [attr.fill]="linearGradientUrl"
        [attr.clipPath]="clipPathUrl"
        [attr.height]="height">
</svg:rect>

<svg:defs>
    <svg:clipPath [attr.id]="clipPathId"></svg:clipPath>

    <svg:linearGradient [attr.id]="linearGradientId">
        <svg:stop ngx-svg-stop
            [offset]="'0%'"
            [values]="'-2; 1'"
            [speed]="content.speed"
            [color]="content.primaryColor">
        </svg:stop>

        <svg:stop ngx-svg-stop
            [offset]="'50%'"
            [values]="'-1.5; 1.5'"
            [speed]="content.speed"
            [color]="content.secondaryColor">
        </svg:stop>

        <svg:stop ngx-svg-stop
            [offset]="'100%'"
            [values]="'-1; 2'"
            [speed]="content.speed"
            [color]="content.primaryColor">
        </svg:stop>
    </svg:linearGradient>
</svg:defs>
`,
  styles: [``]
})
export class RectComponent extends SvgElementComponent {
    @Input() public y: string;
    @Input() public x: string;
    @Input() public rx: string = Defaults.Rx;
    @Input() public ry: string = Defaults.Ry;
    @Input() public width: string;
    @Input() public height: string;
    @Input() public primaryColor: string;
    @Input() public secondaryColor: string;
    @Input() public speed: string;

    constructor(public content: NgxContentLoadingComponent) {
        super(content);
    }
}
